










































































































































































































































































































import Vue from "vue";

import * as CocoSSD from "../coco-ssd/index";
import {
  calculateCameraScaledCoordinates,
  extractBlurCoefficient} from "../utils/imageUtils";
import { RootState } from "./types";

import { performDetection } from "../worker";
import { mainLogic } from "./detection";

declare const cv: any;

window.addEventListener("error", function(e) {
  alert("Error occurred: " + JSON.stringify(e));
  return false;
});

export default Vue.extend({
  name: "Home",
  data(): RootState {
    return {
      cameraButtonEnabled: true,
      model: undefined,
      predictions: [],
      cameraOptions: [],
      blurCoef: 0,
      form: {
        sn: "",
        /* eslint-disable */
        value_t1: "",
        /* eslint-disable */
        value_t2: "",
        /* eslint-disable */
        value_p: "",
        /* eslint-disable */
        value_smns: ""
      },
      debugString: "",
      detectedDeviceType: null,
      detectedTariffType: null,
      initialAnalysisInProgress: false,
      azureAnalysisInProgress: false
    };
  },
  created() {
    // Before we can use COCO-SSD class we must wait for it to finish
    // loading. Machine Learning models can be large and take a moment
    // to get everything needed to run.
    CocoSSD.load()
      .then(loadedModel => {
        this.model = loadedModel;

        this.$notify({
          group: "notifications",
          type: "success",
          title: "Application",
          text: "Application was successfully initialized!"
        } as any);
      })
      .catch(err => {
        alert(
          "Error during model initialization: " +
            err +
            ", " +
            JSON.stringify(err)
        );
      });
  },
  mounted() {

  },
  methods: {
    /**
     * Triggered, when we click on button "Enable Camera"
     */
    enableCam: function() {
      const video = this.$refs.webcam as any;
      const platform = window.navigator.platform;

      if (!navigator.mediaDevices) {
        alert("enumerateDevices() not supported.");
        return;
      }

      let constraints: MediaStreamConstraints | null = null;

      if (platform.includes("Win32")) {
        constraints = {
          video: true,
          audio: false
        };
      } else {
        constraints = {
          video: {
            facingMode: { exact: "environment" }
          },
          audio: false
        };
      }

      navigator.mediaDevices
        .getUserMedia(constraints)
        .then(stream => {
          video.setAttribute("autoplay", "");
          video.setAttribute("muted", "");
          video.setAttribute("playsinline", "");
          video.srcObject = stream;
          video.addEventListener("loadeddata", this.predictWebcam);
          this.cameraButtonEnabled = false;
        })
        .catch(e => alert(e));
    },
    predictWebcam() {
      const sourceImage = this.takepicture();
      const canvas = this.$refs.canvas as any;

      const blurCoef = extractBlurCoefficient(canvas);

      // render blur coefficient to screen
      this.blurCoef = blurCoef;

      // constants (used to configure classification process)
      const maxNumBoxes = 20;
      const minScore = 0.5;
      const model = this.model!;

      const state = this as RootState;

      /* eslint-disable */
      const self = this;

      // run main logic (asynchronously)
      // TODO: move to separate module
      (async function() {
        try {
          // run model and extract initial (imprecise) predictions
          const imprecisePredictions: Array<CocoSSD.DetectedObject> = await performDetection(
            model,
            canvas,
            maxNumBoxes,
            minScore
          );

          // render predictions to screen
          self.renderPredictions(imprecisePredictions);

          // run main detection logic
          await mainLogic(
            imprecisePredictions,
            state,
            blurCoef,
            () => window.requestAnimationFrame(self.predictWebcam),
            self.$notify,
            canvas,
            sourceImage
          );
        } catch (error) {
          alert(
            "error during detection: " + error + ", " + JSON.stringify(error)
          );
        }
      })();
    },
    clearState() {
      // also remove detected images from canvas
      const currentSnCanvas = this.$refs.current_sn as any;
      const currentValueCanvas = this.$refs.current_value as any;
      const currentTariffCanvas = this.$refs.current_tariff as any;

      const clearCanvas = (c: any) =>
        c.getContext("2d").clearRect(0, 0, c.width, c.height);

      clearCanvas(currentSnCanvas);
      clearCanvas(currentValueCanvas);
      clearCanvas(currentTariffCanvas);

      // clear form state
      this.form = {
        /* eslint-disable */
        sn: "",
        /* eslint-disable */
        value_t1: "",
        /* eslint-disable */
        value_t2: "",
        /* eslint-disable */
        value_p: "",
        /* eslint-disable */
        value_smns: ""
      };

      // forget detection state info
      this.detectedDeviceType = null;
      this.detectedTariffType = null;
    },
    renderPredictions(predictions: Array<CocoSSD.DetectedObject>) {
      // clear predictions array
      this.predictions.splice(0, this.predictions.length);

      // add new predictions to list (to display them in UI)
      predictions.forEach(p => this.predictions.push(p));
    },
    takepicture() {
      const video = this.$refs.webcam as any;
      const canvas = this.$refs.canvas as any;

      const { videoWidth, videoHeight } = video;

      const width = 640;
      const height = 480;

      canvas.width = width;
      canvas.height = height;

      const context = canvas.getContext("2d");

      // resize vide image so that it fits to fixed camera view boundaries
      const [
        newX,
        newY,
        newWidth,
        newHeight
      ] = calculateCameraScaledCoordinates(
        width,
        height,
        videoWidth,
        videoHeight
      );

      context.drawImage(
        video,
        0,
        0,
        videoWidth,
        videoHeight,
        newX,
        newY,
        newWidth,
        newHeight
      );

      return cv.imread(canvas);
    }
  }
});
